<template>
<div class="return_menu" >
	<a href="/" class="return_home">
		<img src="/img/home_button.png" alt="">
	</a>
	<div class="back" @click="back">
		<img src="/img/return.png" alt="">
	</div>
</div>
</template>

<script>
export default{
	data(){
		return{
			show: false
		}
	},
	methods:{
		back: function(){
			history.go(-1);
		}
	},
	created: function(){
		
	}
}	
</script>
<style lang="scss" scoped>
.return_menu{
	position: fixed;
	bottom: 20px;
	right: 3%;
	z-index: 10;
}
.return_home{
	width: 35px;
	height: 35px;
	-webkit-tap-highlight-color: rgba(0,0,0,0.5);
	background-color: rgba(0,0,0,0.5);
	display: flex;
	border-radius: 50%;
	border: 2px solid #fff;
	opacity: 0.5;
	margin-bottom: 5px;
	align-items:center;
	justify-content: center;
	img{
		width: 30px;
		height: 30px;
		border-radius: 50%;
		
	}
}
.back{
	@extend .return_home;
}
</style>


