<template>
    <div class="order_item_container">
        <div class="order_item" v-for="(item, index) in orderList">
            <div class="title">
                <img src="img/shop.png" alt="">
                <a :href="item.shopUrl">{{item.shopName}}</a>
                <img src="img/forward.png" alt="" class="forward">
            </div>
            <div class="product_content">
                <div class="product_content--item" v-for="data in item.products">
                    <a :href="data.link">
				<img :src="data.img" alt="">
				<div class="product_data">
					<p>{{data.name}}</p>
					<div class="type">{{data.type}}</div>
					<div class="price">{{data.price}}</div>
				</div>
				<div class="num">x{{data.num}}</div>
			</a>
                </div>
            </div>
            <div class="order_consume">
                {{item.productNum}}: <span>{{item.orderMoney}}</span>
            </div>
            <div class="status">
                <span class="order_id">{{item.orderId}}</span>
                <div class="status_item pay" v-if="item.status == 1" :orderid="item.orderId" @click="goPay(item.orderId)">
                    
                </div>
                <div class="status_item remind" v-if="item.status == 2" :orderid="item.orderId">
                    <span v-show="!reminded[index]" @click="remind($event)" :data-id="index"></span>
                    <span v-show="reminded[index]"></span>
                </div>
                <div class="status_item check" v-if="item.status == 3" :orderid="item.orderId" @click="openLogis">
                    
                </div>
                <div class="status_item evaluate" v-if="item.status == 4" :orderid="item.orderId" @click="evaluate(item.orderId)">
                    
                </div>
                <div class="status_item" v-if="item.status == 5">
                	
                </div>
            </div>
        </div>
        <Logistics :logistics="logistics" v-show="showLogis" v-on:hide="closeLogis"></Logistics>
    </div>
</template>
<script>
import { Toast } from 'mint-ui'
import Logistics from './logistics.vue'
import ProductItem from '../product/product_item.vue'
export default {
    props: ["orderList"],
    components: { Logistics, ProductItem },
    data() {
        return {
            reminded: [],
            showLogis: false,
            logistics: {
                company: "",
                number: 1236546546,
                content: [
                    { text: "", time: "2017-09-12 15:22:31" },
                    { text: "", time: "2017-09-12 15:22:31" },
                    { text: "", time: "2017-09-12 15:22:31" },
                    { text: "", time: "2017-09-12 15:22:31" },
                    { text: "", time: "2017-09-12 15:22:31" },
                    { text: "", time: "2017-09-12 15:22:31" },
                    { text: "", time: "2017-09-12 15:22:31" },
                    { text: "", time: "2017-09-12 15:22:31" },
                    { text: "", time: "2017-09-12 15:22:31" },
                    { text: "", time: "2017-09-12 15:22:31" }
                ]
            }
        }
    },
    created: function() {
        for (var i = 0; i < this.orderList.length; i++) {
            this.reminded.push(false);
        }
        this.logistics.content = this.logistics.content.reverse();
    },
    methods: {
        remind: function(e) {
            Toast({
                message: '',
                duration: 2000
            });
            var id = parseInt(e.currentTarget.getAttribute("data-id"));
            this.reminded.splice(id, 1, true);
        },
        goPay: function(id){
			location.href = "/view/create_order.html?id=" + id
        },
        openLogis: function() {
            this.showLogis = true;
        },
        closeLogis: function() {
            this.showLogis = false;
        },
        evaluate:function(id){
			location.href = "/view/evaluate.html?id=" + id;
        }
    }
}
</script>
<style lang="scss">
.order_item_container {
    width: 100%;
}

.order_item {
    width: 100%;
    box-sizing: border-box;
    padding: 2% 0;
    margin-top: 2%;
    margin-bottom: 5%;
    .title {
        width: 100%;
        display: flex;
        box-sizing: border-box;
        align-items: center;
        padding-left: 2%;
        img {
            width: 35px;
            height: 35px;
            margin: 0 2px;
        }
        a {
            text-decoration: none;
            color: #666;
            font-size: 0.9rem;
        }
        .forward {
            width: 20px;
            height: 20px;
        }
    }
    .order_id {
        color: #999;
        font-size: 0.8rem;
        padding-left: 2%;
        width: 60%;
    }
}

.item {
    @extend .order_item;
}

.product_content--item {
    width: 100%;
    box-sizing: border-box;
    padding: 2% 0;
    background-color: #fcfcfc;
    margin: 0 auto;
    a {
        text-decoration: none;
        width: 100%;
        height: 100px;
        margin-top: 1%;
        display: flex;
        justify-content: space-around;
        align-items: center;
    }
    img {
        width: 80px;
        height: 80px;
    }
    .product_data {
        width: 60%;
        height: 80px;
        p {
            color: #9966CC;
            height: 2em;
            overflow: hidden;
        }
        .type {
            color: #aaa;
            font-size: 0.8em;
            margin-top: 2%;
        }
        .price {
            margin-top: 5%;
            color: #FF6666;
            font-size: 0.8em;
        }
    }
    .num {
        color: #999;
        font-size: 0.9em;
    }
}

.order_consume {
    width: 90%;
    margin: 0 auto;
    text-align: right;
    padding: 8px 0;
    font-size: 0.8rem;
    color: #666;
    box-sizing: border-box;
    padding-right: 2%;
    span {
        font-size: 1rem;
    }
}

.status {
    width: 100%;
    margin: 0 auto;
    border-top: 1px solid #f2f2f2;
    box-sizing: border-box;
    padding-right: 2%;
    height: 45px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .status_item {
        width: 90px;
        height: 28px;
        border-radius: 14px;
        border: 1px solid #999;
        color: #999;
        font-size: 0.9rem;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .pay {
        color: #CC3399;
        border-color: #CC3399;
    }
    .evaluate {
        @extend .pay;
    }
}
</style>


