<template>
    <div class="order">
        <Back style="width: 25px; height: 50px; top:0" imgUrl="/img/back_2.png"></Back>
        <mt-tabbar v-model="selected" class="order_nav">
            <mt-tab-item id="all">
                
            </mt-tab-item>
            <mt-tab-item id="unpay">
                
            </mt-tab-item>
            <mt-tab-item id="unship">
                
            </mt-tab-item>
            <mt-tab-item id="ship">
                
            </mt-tab-item>
            <mt-tab-item id="comment">
                
            </mt-tab-item>
        </mt-tabbar>
        <mt-tab-container v-model="selected" class="order_content">
            <mt-tab-container-item id="all">
                <OrderItem :orderList="orderList"></OrderItem>
            </mt-tab-container-item>
            <mt-tab-container-item id="unpay">
                
            </mt-tab-container-item>
            <mt-tab-container-item id="unship">
                
            </mt-tab-container-item>
            <mt-tab-container-item id="ship">
                
            </mt-tab-container-item>
            <mt-tab-container-item id="comment">
                
            </mt-tab-container-item>
        </mt-tab-container>
        <ReturnHome></ReturnHome>
    </div>
</template>
<script>
import OrderItem from './order_item.vue'
import ReturnHome from '../home/return_home.vue'
import Back from '../product/back.vue'
import Mock from 'mockjs'
import urls from 'urls-js'
var Random = Mock.Random
export default {
    components: { OrderItem, ReturnHome, Back },
    data() {
        return {
            selected: "all",
            orderList: [],

        }
    },
    beforeCreate: function() {

    },
    created: function() {
        var id = urls.parse().query.id || "all";
        var me = this;
        var selArr = ["all", "unpay", "unship", "ship", "comment"];
        if (selArr.indexOf(id) != -1) {
            this.selected = id;
        } else {
            this.selected = "all"
        }
        for (var i = 0; i < 10; i++) {
            this.orderList.push({
                shopUrl: "#",
                shopName: "",
                productNum: 5,
                orderMoney: Random.float(80, 500, 2, 2),
                orderId: Random.natural(1000000000000, 9000000000000),
                status: Random.natural(1, 5),
                reminded: false,
                products: [{
                        link: "product.html",
                        img: Random.image("200x200"),
                        name: "",
                        type: "1",
                        price: Random.float(10, 200, 2, 2),
                        num: Random.natural(1, 5)
                    },
                    {
                        link: "product.html",
                        img: Random.image("200x200"),
                        name: "",
                        type: "1",
                        price: Random.float(10, 200, 2, 2),
                        num: Random.natural(1, 5)
                    }
                ]
            })
        }
        window.onscroll = function() {
            var scrollTopValue = document.documentElement.scrollTop,
                winH = window.innerHeight;
            if (scrollTopValue + winH >= document.body.clientHeight) {
                for (var i = 0; i < 5; i++) {
                    me.orderList.push({
                        shopUrl: "#",
                        shopName: "",
                        productNum: 5,
                        orderMoney: Random.float(80, 500, 2, 2),
                        orderId: Random.natural(1000000000000, 9000000000000),
                        status: Random.natural(1, 4),
                        reminded: false,
                        products: [{
                                link: "product.html",
                                img: Random.image("200x200"),
                                name: "",
                                type: "1",
                                price: Random.float(10, 200, 2, 2),
                                num: Random.natural(1, 5)
                            },
                            {
                                link: "product.html",
                                img: Random.image("200x200"),
                                name: "",
                                type: "1",
                                price: Random.float(10, 200, 2, 2),
                                num: Random.natural(1, 5)
                            }
                        ]
                    })
                }
            }
        }
    }

}
</script>
<style lang="scss">
@import '../style.scss';
* {
    margin: 0;
    padding: 0;
}

.order {
    width: 100%;
    max-width: 640px;
    margin: 0 auto;
    overflow: hidden;
}

.order_nav {
    position: fixed;
    top: 0;
    bottom: auto;
    width: 100%;
    height: 50px;
    max-width: 640px;
    margin: 0 auto;
    z-index: 2;
    display: flex;
    align-items: center;
    box-sizing: border-box;
    padding-left: 4%;
}

.order_content {
    margin-top: 50px;
}
</style>


