<template>
    <div class="product_container">
        <div class="item" v-for="(item, index) in list">
            <div class="title">
                <img src="img/shop.png" alt="">
                <a :href="item.shopUrl">{{item.shopName}}</a>
                <img src="img/forward.png" alt="" class="forward">
            </div>
            <div class="product_content">
                <div class="product_content--item" v-for="data in item.products">
                    <a :href="data.link">
				<img :src="data.img" alt="">
				<div class="product_data">
					<p>{{data.name}}</p>
					<div class="type">{{data.type}}</div>
					<div class="price">{{data.price}}</div>
				</div>
				<div class="num">x{{data.num}}</div>
			</a>
                </div>
            </div>
            <slot></slot>
        </div>
    </div>
</template>
<script>
export default {
    props: ["list"]
}
</script>
<style lang="scss">
*{
	margin:0;
	padding: 0;
}
.item {
    width: 100%;
    box-sizing: border-box;
    padding: 2% 0;
    margin-top: 2%;
    margin-bottom: 5%;
    .title {
        width: 100%;
        display: flex;
        box-sizing: border-box;
        align-items: center;
        padding-left: 2%;
        img {
            width: 35px;
            height: 35px;
            margin: 0 2px;
        }
        a {
            text-decoration: none;
            color: #666;
            font-size: 0.9rem;
        }
        .forward {
            width: 20px;
            height: 20px;
        }
    }
    .order_id {
        color: #999;
        font-size: 0.8rem;
        padding-left: 2%;
        width: 60%;
    }
}
.product_content--item {
    width: 100%;
    box-sizing: border-box;
    padding: 2% 0;
    background-color: #fcfcfc;
    margin: 0 auto;
    a {
        text-decoration: none;
        width: 100%;
        height: 100px;
        margin-top: 1%;
        display: flex;
        justify-content: space-around;
        align-items: center;
    }
    img {
        width: 80px;
        height: 80px;
    }
    .product_data {
        width: 60%;
        height: 80px;
        p {
            color: #9966CC;
            height: 2em;
            overflow: hidden;
        }
        .type {
            color: #aaa;
            font-size: 0.8em;
            margin-top: 2%;
        }
        .price {
            margin-top: 5%;
            color: #FF6666;
            font-size: 0.8em;
        }
    }
    .num {
        color: #999;
        font-size: 0.9em;
    }
}
</style>


