<template>
<div class="logistics_container">
	<div class="content">
		<div class="top">
			<p>{{logistics.company}}</p>
			<p class="number"><span id="copynum">{{logistics.number}}</span></p>
		</div>
		<div class="bottom">
			<div class="item" v-for="item in logistics.content">
				<p>{{item.text}}</p>
				<p>{{item.time}}</p>
			</div>
		</div>
		<div class="close" @click="close">
			<img src="img/close.png" alt="">
		</div>
	</div>
</div>
</template>

<script>
export default{
	props: ["logistics"],
	methods: {
		close: function(){
			this.$emit("hide");
			console.log(1)
		}
	}
}
</script>

<style lang="scss">
.logistics_container{
	width: 100%;
	max-width: 640px;
	margin: 0 auto;
	height: 100vh;
	position: fixed;
	top: 0;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: #fff;
	z-index: 10;
	background-color: rgba(0,0,0,0.5);
	.content{
		width: 70%;
		height: 80vh;
		border-radius: 8px;
		background-color: #fff;
		position: relative;
		animation: scaleIn 0.3s;
		animation-fill-mode: both;
		.top{
			width: 100%;
			height: 15vh;
			box-sizing: border-box;
			padding: 0 5%;
			padding-top: 10%;
			background-color: #993399;
			color: #fff;
			font-size: 0.8rem;
			border-radius: 8px 8px 0 0;
			
			.copy{
				padding: 2px 7px;
				border: 1px solid #fff;
				margin-left: 5px;
			}
		}
		.bottom{
			height: 50vh;
			margin-top: 2vh;
			width: 100%;
			overflow-y: auto; 
			.item{
				width: 85%;
				margin-left: 5%;
				box-sizing: border-box;
				padding-left: 10%;
				padding-bottom: 5%; 
				border-left: 1px solid #ddd;
				position: relative;
				&:before{
					width: 12px;
					height: 12px;
					border-radius: 50%;
					background-color: #ddd;
					content: "";
					display: block;
					position: absolute;
					left: -6px;
					top: 2px;
				}
				&:first-of-type:before{
					background-color: #993399;
				}
				p{
					color: #993399;
					font-size: 0.8rem;
				}
			}
		}
		.close{
			width: 100%;
			height: 50px;
			position: absolute;
			bottom: 12px;
			display: flex;
			justify-content: center;
			align-items: center;
			img{
				box-sizing: border-box;
				width: 45px;
				height: 45px;
				border-radius: 50%;
				border: 2px solid #993399;
			}
		}
	}
}
@keyframes scaleIn{
	0%{
		transform: scale(0);
	}
	100%{
		transform: scale(1);
	}
}
</style>


